








import Vue from 'vue';
import QrScanner from '@/pages/QrScanner.vue'

export default Vue.extend({
  name: 'App',
  components: {
    QrScanner,
  },

  data() {
    return {
      disabled: false
    };
  },

  mounted() {
    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState === 'hidden') {
        this.disabled = true;
      } else if (document.visibilityState === 'visible') {
        this.disabled = false;
      }
    });
  },
});
